import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import {
  IndentButton,
} from '@ali/we-toolbar';
import { PluginRoles } from '@ali/4ever-bamboo';
import {
  DECREASE_HOT_KEY_TIP,
  INCREASE_HOT_KEY_TIP,
} from '../utils/hotKeyTips';
;
import { increaseIndent, decreaseIndent } from '../actions';

export interface IndentButtonProps {
  controller: Controller;
  locale: Record<string, string>;
}

const titles = {
  increase: INCREASE_HOT_KEY_TIP,
  decrease: DECREASE_HOT_KEY_TIP,
};
const MemoIndentButton = React.memo(IndentButton);

export default function Button(props: IndentButtonProps) {
  const { controller, locale, ...rest } = props;
  const isInc = true; // 默认始终增加缩进

  const onSelect = React.useCallback((value: string) => {
    controller.run('onAction', value === 'increase' ? increaseIndent() : decreaseIndent());
  }, [controller]);

  const handleClick = React.useCallback(() => {
    controller.run('onAction', increaseIndent());
  }, [controller]);

  const { isCollapsedAtEdge } = controller.value.selection;
  const disabled = isCollapsedAtEdge || controller.query('isSelectionInListSymbol');

  const tip = isInc
    ? `${locale.rightButtonTitle} ${INCREASE_HOT_KEY_TIP}`
    : `${locale.leftButtonTitle} ${DECREASE_HOT_KEY_TIP}`;

  return (
    <MemoIndentButton
      {...rest}
      shortcutText={tip}
      titles={titles}
      disabled={disabled}
      testid="bi-toolbar-indent"
      onClick={handleClick}
      onSelect={onSelect}
      role={PluginRoles.indent}
    />
  );
}
